import React from "react";
import Moment from "react-moment";
import Countdown,{zeroPad} from 'react-countdown';
import * as utils from '../common/utils';
import { number } from "prop-types";
class SettingTop extends React.Component {  
  state = {
    id: this.props.prop.id,
    gameType: this.props.prop.gt,
    gameId: this.props.prop.gi,
    featured: this.props.prop.f,
    contestType: this.props.prop.ct,
    st: this.props.prop.st,
    t: this.props.prop.t,
    users: this.props.prop.u,
    userMax: this.props.prop.um,
    percent: Math.ceil(this.props.prop.u / this.props.prop.um * 100),
    totalPrize: this.props.prop.tp,
    fee: this.props.prop.fe,
    topValue : this.props.prop.tv,
  };  
  render() {        
    const {gameType, id, gameId, featured, contestType, st, t, users, userMax, percent, totalPrize, fee, topValue} = this.state;                    
    //console.log(this.props);
    return(    
        <div className="setting-top">          
            <div className="tit featured">
              <div className={"info-txt game-"+gameType}>
                  <span className="tit">{utils.getMenu(Number(gameType))} SCORE DERBY</span>                    
                  <span className="date"><Moment format="ddd, MMM D, hh:mm A z">{st}</Moment></span> 
                  <span className="reward-bonus bonus-open"></span>
              </div>
              <div className="team-txt">{utils.getMenu(Number(gameType))} - {t}  </div>                                                                     
            </div>
            <div className="tit-group">
              <div className="titbox">
                <span className="tit">Starts in</span>                
                <Countdown date={st} renderer={
                  st => <p className="time">{st.days || 0 ? (st.days+"d "):("")}{zeroPad(st.hours)}:{zeroPad(st.minutes)}:{zeroPad(st.seconds)}</p>
                } />                
              </div>
              <div className="grp-box">
                <div className="grp-txt">
                  <span className="txt">Entries</span>
                  <div className="sum">{users} / {userMax}</div>
                  <div className="sum-percent"><span className="count">{percent}</span>%</div>
                </div>          
                <div className="grp"><span className="grp-bar" style={{width: percent+"%"}}></span></div>																				
              </div> 
              <div className="titbox">
                <span className="tit">In Prizes</span>
                <p><span className="asset-121002">{totalPrize.toLocaleString()}</span></p>
              </div>
              <div className="titbox">
                <span className="tit">Single Entry</span>
              <p><span className="asset-121002">{fee.toLocaleString()}</span></p>
              </div>                  
            </div>                                         
        </div>
    )
  }
};
export default SettingTop;