import React from "react";
import axios from "axios";
import * as utils from '../../common/utils';
import SelectTeam from "./SelectTeam";
class Condition extends React.Component {
  state = {      
    playerlist : [],                 
    gameid: this.props.settinginfo.gi,
    gametype: this.props.settinginfo.gt,
    homeabbr: this.props.settinginfo.hta,
    awayabbr: this.props.settinginfo.ata,
    hometeam: this.props.settinginfo.htn,
    awayteam: this.props.settinginfo.atn     
  };
  getTeamStat= async (gametype, gameid) =>{
    const {data : {result: teamStat }} = await axios.get(utils.DATA_URL+`/stat/team/${gametype}/${gameid}`);
    this.setState({      
      teamStatH: teamStat.stat[0],      
      teamStatA: teamStat.stat[1],
      playerlist : this.props.settinginfo.pl
    }); 

    //console.log(this.state.teamStat.stat[1]);
  };
  componentDidMount(){
    this.getTeamStat(this.props.settinginfo.gt, this.props.settinginfo.gi);     
  }; 
  render() {        
    const {playerlist, gameid, gametype, homeabbr, awayabbr, hometeam, awayteam} = this.state;    
    console.log(playerlist);

    return(    
        <div className="condition-content">         
            <SelectTeam {...this.props.settinginfo} teamStatH={this.state.teamStatH} teamStatA={this.state.teamStatA} />
            <div className="select-content">
              <p className="select-tit">Select Batters</p>
              {gametype===104002 && (//mlb
                <div className="select-team">
                  <div className="team first-team active">
                    <span id="firstImg" className="">PHI<em>(0)</em></span>                                    
                  </div>			
                  <div className="team second-team">                 
                    <span id="secondImg" className="">NYY<em>(0)</em></span>
                  </div>
                </div>
              )}
              {gametype===104012 && (//kbo
                <div className="select-team">
                  <div className="team first-team active">
                    <span id="firstImg" className="">{awayabbr}<em>(0)</em></span>                                    
                  </div>			
                  <div className="team second-team">                 
                    <span id="secondImg" className="">{homeabbr}<em>(0)</em></span>
                  </div>
                </div>
              )}
              <p className="select-txt">At least one player from both teams must be selected.</p>
              <div className="select-player">
                <div className="tb-tit">	
                  <span className="no">No</span>
                  <span className="pos">Pos</span>
                  <span className="starters">Starters</span>
                  <span className="name">Name</span>
                  <span className="fppg">FPPG</span>
                  <span className="pick">Pick %</span>
                  <span></span>	
                </div>
                <ul className="tb-txt">
                {playerlist.map((player, index) => {                                
                  return (                                                                                    
                    <li key={index}>
                      <span className="no">{player.nu}</span>                  
                      <span className="pos">{player.po}</span>
                      <span className="starters"></span>
                      <span className="name"><em></em>{player.nm}</span>
                      <span className="fppg">{player.fa}</span>	
                      <span className="pick">{Math.round(player.pir * 100) / 100}%</span>	
                      <span>																														
                        <button className="btn-plus-minus" type="button">
                          <span className="icon-wrapper">
                            <span className="icon-bar"></span>
                            <span className="icon-bar"></span>													
                          </span>                        
                        </button>																					
                      </span>
                    </li>  
                  )                                                    
                })} 
                                  
                </ul>
              </div>

            </div>
            <ul className="txt-box">
              <li>A 10% commission will be collected from your Entry Fee by RankingBall.</li>
              <li>User-created Contests will be refunded and converted to Free Contest unless three or more players enter.(TOURNAMENT, Head-to-Head)</li>
              <li>User-created Contests cannot be canceled(and refunded) by the user.</li>
              <li>Submitted Entries can be modified in the ‘Upcoming’ tab up to 5 minutes before the real game starts.</li>             
            </ul>                           
        </div>
    )
  }
};
export default Condition;